@import "./colors.scss";

.personalDataBlock {
  border-top: solid 5px;
  border-radius: 10% 10% 0 0;
  padding: 10px;
  &.negative {
    border-top-color: $green;
  }
  &.positive {
    border-top-color: $red;
  }
  background-color: rgba($bgOrangeColor, 0.6);

  h1 {
    text-align: center;
    font-size: 35px;
    padding: 15px 0;
  }

  div {
    ul {
      list-style: none;
      margin: 0;
      padding: 0;

      li {
        margin: 10px 0;
        font-size: 18px;
        strong {
          text-transform: capitalize;
        }
        .negative {
          color: $green;
          font-weight: 700;
        }
        .positive {
          color: $red;
          font-weight: 700;
        }
      }
    }
  }
}

.container {
  width: 85%;
  position: relative;
  margin: 0 auto;
}

.blockInfo {
  padding: 20px 0;

  .content {
    display: flex;
    justify-content: space-between;
    padding: 15px 0;

    ul {
      margin: 0;
      padding: 0;
      list-style: none;

      li {
        &.header {
          display: flex;
          align-items: center;
          margin-bottom: 20px;

          &.negative {
            .icon,
            .description {
              color: $green;
            }
          }
          &.positive {
            .icon,
            .description {
              color: $red;
            }
          }
          .icon {
            font-size: 70px;
            margin-right: 10px;
          }
          .description {
            font-size: 25px;
            font-weight: 500;
          }
        }
        &:not(.header) {
          font-size: 18px;
          line-height: 27px;
          margin-bottom: 10px;
          &:last-child {
            margin-bottom: 5px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 800px) {
  .personalDataBlock {
    border-top: solid 10px;

    padding: 1vw 1px 5vw;

    h1 {
      font-size: 6vw;
      padding: 1.5vw 0;
    }

    div {
      ul {
        li {
          margin: 2vw 0;
          font-size: 3vw;
        }
      }
    }
  }

  .container {
    width: 90%;
  }

  .blockInfo {
    padding: 4vw 0;

    .content {
      flex-direction: column-reverse;
      justify-content: space-between;
      padding: 3vw 0;

      ul {
        li {
          &.header {
            display: flex;
            align-items: center;
            margin-bottom: 4vw;

            .icon {
              font-size: 16vw;
              margin-right: 5vw;
            }
            .description {
              font-size: 7vw;
            }
          }
          &:not(.header) {
            font-size: 3vw;
            line-height: 5vw;
            margin-bottom: 1vw;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 540px) {
  .personalDataBlock {
    border-top: solid 5px;

    h1 {
      font-size: 6vw;
      padding: 1.5vw 0;
    }

    div {
      ul {
        li {
          margin: 2.5vw 0;
          font-size: 3.5vw;
        }
      }
    }
  }

  .blockInfo {
    padding: 4vw 0;

    .content {
      padding: 3vw 0;

      ul {
        li {
          &.header {
            display: flex;
            align-items: center;
            margin-bottom: 4vw;

            .icon {
              font-size: 18vw;
              margin-right: 5vw;
            }
            .description {
              font-size: 7vw;
            }
          }
          &:not(.header) {
            font-size: 3.5vw;
            line-height: 5vw;
            margin-bottom: 1vw;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 375px) {
  .personalDataBlock {
    h1 {
      font-size: 7vw;
      padding: 2vw 0;
    }

    div {
      ul {
        li {
          margin: 2.5vw 0;
          font-size: 4vw;
        }
      }
    }
  }

  .blockInfo {
    padding: 4vw 0;

    .content {
      padding: 3vw 0;

      ul {
        li {
          &.header {
            display: flex;
            align-items: center;
            margin-bottom: 4vw;

            .icon {
              font-size: 18vw;
              margin-right: 5vw;
            }
            .description {
              font-size: 8vw;
            }
          }
          &:not(.header) {
            font-size: 4vw;
            line-height: 5vw;
            margin-bottom: 2vw;
          }
        }
      }
    }
  }
}
