.header {
  width: 100%;
  padding: 20px 10px;
}

.containerLogo {
  position: relative;
  margin: 0 auto;
  width: 300px;
  img {
    width: 100%;
  }
}

@media screen and (max-width: 800px) {
  .header {
    width: 100%;
    padding: 4vw 1vw;
  }

  .containerLogo {
    width: 50vw;
    img {
      width: 100%;
    }
  }
}

@media screen and (max-width: 540px) {
  .header {
    width: 100%;
    padding: 4vw 1vw;
  }

  .containerLogo {
    width: 60vw;
    img {
      width: 100%;
    }
  }
}

@media screen and (max-width: 375px) {
  .header {
    width: 100%;
    padding: 4vw 1vw;
  }

  .containerLogo {
    width: 70vw;
    img {
      width: 100%;
    }
  }
}
