@import "./colors.scss";

.pageContainer {
  &.negative {
    box-shadow: 0 0 5px $green;
  }
  &.positive {
    box-shadow: 0 0 5px $red;
  }
  width: 100%;
  min-height: 90%;
  position: relative;
  max-width: 800px;

  border-radius: 5px;
}

.error {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;

  div {
    text-align: center;
    .info {
      font-size: 4vw;
      color: #aaa;
      display: block;
      cursor: default;
    }
    .link {
      text-decoration: none;
      display: block;
      font-size: 1.5vw;
      font-weight: 500;
      margin-top: 2vw;
      color: #aaa;
      text-decoration: underline;
      cursor: pointer;

      &:hover {
        color: #000;
      }
    }
  }
}

@media screen and (max-width: 800px) {
  .pageContainer {
    width: 100%;
    position: relative;
    max-width: 100%;
    border: none;
    box-shadow: none;
    &.negative {
      box-shadow: none;
    }
    &.positive {
      box-shadow: none;
    }
  }
}
