@import "./colors.scss";

.footer {
  width: 100%;
  padding: 20px 40px;
  background-color: $bgOrangeColor;
  display: flex;
  justify-content: space-between;
  align-items: center;

  .leftBlock {
    display: flex;
    align-items: center;
    .icon {
      margin-right: 10px;
      font-size: 20px;
    }
    .description {
      font-size: 17px;
      text-transform: uppercase;
    }
  }

  .rightBlock {
    ul {
      margin: 0;
      padding: 0;
      list-style: none;
      display: flex;
      li {
        font-size: 17px;
        &:after {
          content: "-";
          display: inline-block;
          margin: 0 5px;
          color: #000;
          font-weight: 400;
        }
        &:last-child {
          &:after {
            display: none;
          }
        }

        font-weight: 500;
        cursor: pointer;
        &:first-child {
          list-style: none;
        }
        text-transform: uppercase;
        &.active {
          color: $orange;
          font-weight: bold;
        }

        &:not(.active):hover {
          opacity: 0.6;
        }
        &:last-child {
          padding-right: none;
        }
      }
    }
  }
}

@media screen and (max-width: 800px) {
  .footer {
    width: 100%;
    padding: 4vw 7vw;
    flex-wrap: wrap;

    .leftBlock {
      .icon {
        margin-right: 2vw;
        font-size: 4vw;
      }
      .description {
        font-size: 3vw;
      }
    }

    .rightBlock {
      ul {
        li {
          font-size: 3vw;
          &:not(.active):hover {
            opacity: 0.6;
          }
          &:last-child {
            padding-right: none;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 540px) {
  .footer {
    width: 100%;
    padding: 5vw 8vw;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;

    .leftBlock {
      .icon {
        margin-right: 2vw;
        font-size: 5vw;
      }
      .description {
        font-size: 4vw;
      }
    }

    .rightBlock {
      margin-top: 3vw;

      ul {
        li {
          font-size: 4vw;
        }
      }
    }
  }
}
